<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card id="loading-container" class="vs-con-loading__container mb-base">
      <div class="flex flex-wrap-reverse items-center mb-base">
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
        <template v-for="transition in transitions">
          <vs-button v-bind:key="transition.toStatusId" :disabled="statusId" @click="selectTransition(transition)" class="p-3 mr-4" :color="transitionButtonVariant(transition)">
            {{ transition.transitionName }}
          </vs-button>
        </template>
      </div>

      <div class="inline-form p-4 mb-4" v-if="statusId">
        <form v-on:keyup.enter="modifyWorkflowTask">
          <div class="nv-row mb-4" v-if="task.variableId">
            <div class="nv-col w-full">
              <label class="vs-input--label">{{ $t('VALUE') }}</label>
              <vs-input v-if="task.variableTypeEnumId === 'WF_VAR_NUMBER'" class="w-full" type="number" v-model="definedValue"/>
              <vs-input v-else class="w-full" v-model="definedValue"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <label class="vs-input--label">{{ $t('COMMENTS') }}</label>
              <vs-textarea class="w-full" v-model="comments"/>
            </div>
          </div>
          <div class="nv-row mb-4" v-if="workflowTaskAuth === 'EMAIL' || workflowTaskAuth === 'SMS'">
            <div class="nv-col w-full required">
              <label class="vs-input--label">{{ $t('ONE_TIME_PASSWORD') }}</label>
              <vs-input ref="password" class="w-full" v-model="password"/>
            </div>
          </div>
          <div class="nv-row mb-4" v-if="workflowTaskAuth === 'PWD'">
            <div class="nv-col w-full required">
              <label class="vs-input--label">{{ $t('PASSWORD') }}</label>
              <password class="w-full" v-model="password" placeholder="" :toggle="true" :badge="false" :showStrengthMeter="false" autocomplete="off"/>
            </div>
          </div>
          <div class="nv-row">
            <div class="nv-col sm:w-1/2 w-full">
              <div class="flex justify-left">
                <vs-button class="px-5 py-3 mr-2" @click="modifyWorkflowTask" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
                <vs-button class="px-5 py-3 mr-2" @click="statusId = null" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
              </div>
            </div>
          </div>
         </form>
      </div>

      <div class="nv-row row hover">
        <div class="nv-col w-full">
          <p class="info-row">
            <span>{{ $t('ID') }}: </span>
            <span>{{ task.taskId | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('WORKFLOW_NAME') }}: </span>
            <span>{{ task.workflowName | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('TASK_TYPE') }}: </span>
            <span>{{ task.taskTypeDescription | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('ACTION_TYPE') }}: </span>
            <span>{{ task.actionTypeDescription | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('STATUS') }}: </span>
            <span>{{ task.statusDescription | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('CREATION_DATE') }}: </span>
            <span>{{ task.creationDate | formatUnixDate }}</span>
          </p>
          <p class="info-row" v-if="task.instanceTimeoutDate">
            <span>{{ $t('DUE_DATE') }}: </span>
            <span>{{ task.instanceTimeoutDate | formatUnixDate }}</span>
          </p>
          <p class="info-row" v-if="task.variableName">
            <span>{{ $t('VARIABLE_NAME') }}: </span>
            <span>{{ task.variableName | placeholder('-') }}</span>
          </p>
          <p class="info-row" v-if="task.variableDescription">
            <span>{{ $t('DESCRIPTION') }}: </span>
            <span>{{ task.variableDescription | placeholder('-') }}</span>
          </p>
          <p class="info-row" v-if="task.variableDefinedValue">
            <span>{{ $t('VALUE') }}: </span>
            <span>{{ task.variableDefinedValue | placeholder('-') }}</span>
          </p>
          <p class="info-row" v-if="task.summary">
            <span>{{ $t('SUMMARY') }}: </span>
            <span>{{ task.summary | placeholder('-') }}</span>
          </p>
          <p class="info-row" v-if="task.description">
            <span>{{ $t('DESCRIPTION') }}: </span>
            <span>{{ task.description | placeholder('-') }}</span>
          </p>
          <p class="info-row" v-if="task.creationDate">
            <span>{{ $t('CREATION_DATE') }}: </span>
            <span>{{ task.creationDate | formatUnixDate }}</span>
          </p>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import EnumerationService from '@/services/commons/EnumerationService'
import SecurityService from '@/services/nucleus/SecurityService'
import WorkflowService from '@/services/commons/WorkflowService'
import StatusFlowService from '@/services/commons/StatusFlowService'

export default {
  name: 'workflow-task-view',
  mixins: [ListLayoutMixin, DisabledClassMixin, AlertMixin, LoadingMixin],
  data: function () {
    return {
      task: {},
      workflowTaskAuth: process.env.VUE_APP_WF_TASK_AUTH,
      transitions: [],
      statusId: null,
      definedValue: '',
      comments: '',
      password: ''
    }
  },
  methods: {
    async load () {
      let self = this
      EnumerationService.enumerations({
        enumTypeId: 'WorkflowVariableType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.variableTypes = response.data.enumerationList || []
      })

      this.startLoading('loading-container')
      WorkflowService.workflowTasks({
        taskId: this.$route.params.id
      }).then(function (response) {
        self.task = response.data.taskList[0] || {}
        self.loadTransitions()
      })
    },
    loadTransitions () {
      let self = this
      StatusFlowService.statusFlowTransitions({
        statusFlowId: 'WorkflowTaskStatusFlow',
        fromStatusId: this.task.statusId,
        conditionExpression: this.task.taskTypeEnumId,
        orderByField: 'transitionSequence'
      }).then(function (response) {
        self.transitions = response.data.transitionList || []
      })
    },
    transitionButtonVariant (transition) {
      if (transition.toStatusId === 'WF_TASK_APPROVE' || transition.toStatusId === 'WF_TASK_DONE') {
        return 'success'
      } else if (transition.toStatusId === 'WF_TASK_REJECT') {
        return 'danger'
      } else {
        return 'primary'
      }
    },
    selectTransition (transition) {
      this.statusId = transition.toStatusId

      if (this.workflowTaskAuth === 'EMAIL' || this.workflowTaskAuth === 'SMS') {
        let self = this
        let sendEmail = this.workflowTaskAuth === 'Email'
        let sendSms = this.workflowTaskAuth === 'SMS'
        this.startLoading('loading-container')
        SecurityService.generateOneTimePassword({
          sendEmail: sendEmail,
          sendSms: sendSms
        }).then(function (response) {
          self.stopLoading('loading-container')
          self.$refs.password.$el.childNodes[1].childNodes[0].focus()
        }).catch(function (error) {
          self.stopLoading('loading-container')
          self.showDangerAlert(error.response.data.errors)
        })
      }
    },
    modifyWorkflowTask () {
      let self = this
      this.startLoading('loading-container')
      WorkflowService.modifyWorkflowTask({
        taskId: this.$route.params.id,
        statusId: this.statusId,
        definedValue: this.definedValue,
        comments: this.comments,
        password: this.password
      }).then(function (response) {
        self.stopLoading('loading-container')
        if (response.status === 200) {
          self.statusId = null
          self.comments = ''
          self.password = ''
          self.load()
        }
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
