import Endpoint from '@/services/Endpoint'

export default {
  workflows (payload) {
    return Endpoint().get('/s1/commons/workflow', {
      params: payload
    })
  },
  workflowStats (payload) {
    return Endpoint().get('/s1/commons/workflow/stats', {
      params: payload
    })
  },
  createWorkflow (payload) {
    return Endpoint().post('/s1/commons/workflow', payload)
  },
  modifyWorkflow (payload) {
    return Endpoint().patch('/s1/commons/workflow', payload)
  },
  approveWorkflow (payload) {
    return Endpoint().post(`/s1/commons/workflow/${payload.workflowId}/approve`, payload)
  },
  duplicateWorkflow (payload) {
    return Endpoint().post(`/s1/commons/workflow/${payload.workflowId}/duplicate`, payload)
  },
  designWorkflow (payload) {
    return Endpoint().post(`/s1/commons/workflow/${payload.workflowId}/design`, payload)
  },
  draftWorkflow (payload) {
    return Endpoint().post(`/s1/commons/workflow/${payload.workflowId}/draft`, payload)
  },
  workflowVariables (payload) {
    return Endpoint().get(`/s1/commons/workflow/${payload.workflowId}/variable`, {
      params: payload
    })
  },
  createWorkflowVariable (payload) {
    return Endpoint().post(`/s1/commons/workflow/${payload.workflowId}/variable`, payload)
  },
  workflowInitiators (payload) {
    return Endpoint().get(`/s1/commons/workflow/${payload.workflowId}/initiator`, {
      params: payload
    })
  },
  createWorkflowInitiator (payload) {
    return Endpoint().post(`/s1/commons/workflow/${payload.workflowId}/initiator`, payload)
  },
  expireWorkflowInitiator (payload) {
    return Endpoint().delete(`/s1/commons/workflow/${payload.workflowId}/initiator`, {
      params: payload
    })
  },
  workflowTasks (payload) {
    return Endpoint().get('/s1/commons/workflow/task', {
      params: payload
    })
  },
  countWorkflowTasks (payload) {
    return Endpoint().get('/s1/commons/workflow/task/count', {
      params: payload
    })
  },
  modifyWorkflowTask (payload) {
    return Endpoint().patch('/s1/commons/workflow/task', payload)
  },
  workflowTypes (payload) {
    return Endpoint().get('/s1/commons/workflow/type', {
      params: payload
    })
  },
  workflowTypeServices (payload) {
    return Endpoint().get('/s1/commons/workflow/type/service', {
      params: payload
    })
  },
  workflowTypeAlertTemplates (payload) {
    return Endpoint().get('/s1/commons/workflow/type/alertTemplate', {
      params: payload
    })
  },
  draftWorkflows (payload) {
    return Endpoint().get(`/s1/commons/workflow/${payload.workflowId}/draft`, {
      params: payload
    })
  },
  designDraftWorkflow (payload) {
    return Endpoint().post(`/s1/commons/workflow/${payload.workflowId}/design/draft`, payload)
  },
  deleteDraftWorkflow (payload) {
    return Endpoint().delete(`/s1/commons/workflow/${payload.workflowId}/design/draft`, {
      params: payload
    })
  }
}
