import Endpoint from '@/services/Endpoint'

export default {
  statusFlows (payload) {
    return Endpoint().get('/s1/commons/basic/status/flow', {
      params: payload
    })
  },
  statusFlowItems (payload) {
    return Endpoint().get(`/s1/commons/basic/status/flow/${payload.statusFlowId}/item`, {
      params: payload
    })
  },
  createStatusFlowItem (payload) {
    return Endpoint().post(`/s1/commons/basic/status/flow/${payload.statusFlowId}/item`, payload)
  },
  initialStatusFlowItem (payload) {
    return Endpoint().patch(`/s1/commons/basic/status/flow/${payload.statusFlowId}/item`, payload)
  },
  deleteStatusFlowItem (payload) {
    return Endpoint().delete(`/s1/commons/basic/status/flow/${payload.statusFlowId}/item`, {
      params: payload
    })
  },
  statusFlowTransitions (payload) {
    return Endpoint().get(`/s1/commons/basic/status/flow/${payload.statusFlowId}/transition`, {
      params: payload
    })
  },
  createStatusFlowTransition (payload) {
    return Endpoint().post(`/s1/commons/basic/status/flow/${payload.statusFlowId}/transition`, payload)
  },
  deleteStatusFlowTransition (payload) {
    return Endpoint().delete(`/s1/commons/basic/status/flow/${payload.statusFlowId}/transition`, {
      params: payload
    })
  }
}
